body{
  margin: 0;
  padding: 5%;
  #fullpage{
    width : 100%;
    height: 80vh;
    border: 1px solid black;
    .mainWrap{
      width: 100%;
      height: 100%;
      .sideBarWrap{
        position: relative;
        display: block;
        text-align: center;
        width: 300px;
        height: 100%;
        .sideBarTop{
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 20%;
          .home{
            margin: 10px 0 10px 0;
            border: 1px solid black;
            border-radius: 50px;
            width: 100px;
            height: 100px;
            display: flex;
            justify-content: center;
            align-items: center;
          }
        }
        .sideBarCont{
          display: block;
          width: 100%;
          height: 80%;
          border: 1px solid black;
          display: flex;
          flex-direction: column;
          .sidecont {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;
            border: 1px solid black;
          }
        }
      }
    }
  }
}